.tabs {
	background-color: #f5f5f5;
	padding-top: 145px;
	padding-bottom: 115px;
}

.tabs__wrap {
	display: flex;
	justify-content: space-between;		
}

.tabs__list {
	list-style: none;
	padding: 0;
	margin: 0;
}

.tabs__button {
	@include button() {
		background-color: $primary;
		border: 1px solid #c7c7c7;
    width: 110px;
    height: 110px;
    font-size: 15px;
    line-height: 1.333;
    color: #fff;
    font-weight: bold;
		text-align: center;
		text-transform: uppercase;
		cursor: 0;
	};
	&:hover, &:active {
		background-color: none;
		border-color: none;
	}
}

.tabs__button--grey {
	background-color: #333333;
}

.tabs__button--down {
	width: 120px;
	height: 45px;
	text-transform: none;
	border-radius: 4px;
	margin-top: 50px;

	&:hover, &:active {
		background-color: darken($primary, 15%);
		border-color: darken($primary, 15%);
	}
}

.tabs__info {
	margin-left: 100px;
}

.tabs__title {
	font-size: 20px;
	color: rgb(0, 0, 0);
	font-weight: bold;
	line-height: 1.2;
	text-align: left;
	margin: 10px 0 40px 0;	
}

.tabs__text {
	font-size: 14px;
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: left;	
	margin-top: 20px;
}

.tabs__picture {
	margin-left: 100px;
	position: relative;
	
	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		background-color:#f5f5f5;
		opacity: 0;
	}
}

@media (max-width: 980px) {
	.tabs__picture {
		display: none;
	}

	.tabs__wrap {
		align-items: center;
	}

	.tabs {
		padding-top: 100px;
		padding-bottom: 70px;
	}

}

@media (max-width: 480px) {
	.tabs {
		padding-top: 20px;
		padding-bottom: 25px;
	}

	.tabs__element {
		margin: 60px 0;
	}

	.tabs__info {
		margin-left: 50px;
	}

	.tabs__title {
		margin-bottom: 20px;
	}

	.tabs__button--down {
		margin-top: 25px;
	}
}

