.socials {
	background-color: #fff;
	border-bottom: 1px solid #b7b7b7;
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.38);
}

.socials__wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;		
}

.socials__left {
	 flex-basis: 370px;
}

.socials__title {	
	font-size: 20px;
	color: rgb(51, 51, 51);
	font-weight: bold;
	line-height: 1.2;
	text-align: left;
	margin: 20px 0 5px 0;	
}

.socials__description {
	font-size: 14px;
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: left;	
	margin: 0 0 20px 0;
}

.socials__list {
	list-style: none;
	padding: 0;
	display: flex;
	align-items: flex-end;
}

.socials__element {
	margin: 0 30px;

	&:first-child {
		margin-left: 0;
	}

	&:last-child {
		margin-right: 0;
	}
}

.socials__link {
	font-size: 48px;
	color: rgb(204, 204, 204);
	line-height: 1.2;
	transition-duration: 500ms;
	transition-property: color;

	&:hover {
		color: $primary;
	}
}

@media (max-width: 1198px) {
	.socials__link {
		font-size: 40px;
	}

	.socials__element {
		margin: 0 25px;
	}
}

@media (max-width: 980px) {
	.socials__wrap {
		flex-direction: column;
		justify-content: flex-start;
		padding-top: 25px;
		padding-bottom: 25px;
	}

	.socials__link {
		font-size: 35px;
	}

	.socials__left {
		flex-basis: auto;
	}
}

@media (max-width: 768px) {
	.socials__wrap {
		padding-top: 15px;
		padding-bottom: 15px;
	}

	.socials__element {
		margin: 0 18px;
		&:first-child {
			margin-left: 0;
		}
	}
}

@media (max-width: 480px) {
	.socials__title {
		margin: 0 0 5px 0;
	}

	.socials__link {
		font-size: 20px;
	}

	.socials__element {
		margin: 0 12px;
	}

	.socials__description {
		margin: 0;
	}

	.socials__list {
		margin-bottom: 0;
		flex-wrap: wrap;
	}
}