.sub {
	background-color: #fff;
}

.sub__wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 100px;
	padding-bottom: 130px;		
}


.sub__img {
	margin-left: 0;
}

.sub__foto {
	box-shadow: 0 0 16px rgba(0, 0, 0, .5);
}

.sub__info {
	margin-left: 190px;
}

.sub__title {
	font-size: 20px;
	color: rgb(33, 33, 33);
	font-weight: bold;
	line-height: 2.15;
	margin-bottom: 30px;

	&::after {
		content: '';
		width: 65px;
		height: 2px;
		background: $primary;
		display: block;
		margin-top: 20px;
	}
}

.sub__text {
	font-size: 14px;
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: left;
	margin-bottom: 50px;	
}

.sub__list {
	display: flex;
	justify-content: start;
	margin-top: 30px;
}

.sub__element {
	display: flex;
	justify-content: center;
}

.sub__element i {
	width: 40px;
	height: 40px;
	font-size: 16px;
	border: 2px solid #ff8b38;
	border-radius: 50%;
	line-height: 1.2;
	color: #ff8b38;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}

.sub__subtitle {
 	margin: 0 0 0 30px;
	font-size: 14px;
	color: rgb(33, 33, 33);
	line-height: 1.214;
}

.sub__desc {
	margin-left: 30px;
	margin-top: 5px;
	font-size: 14px;		
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: left;	
}

@media (max-width: 980px) {
	.sub__wrap {
		padding-top: 50px;
		padding-bottom: 65px;
	}

	.sub__info {
		margin-left: 50px;
	}
}

@media (max-width: 768px) {
	.sub__wrap {
		flex-direction: column;
		justify-content: flex-start;
	}

	.sub__info {
		margin-left: 0;
	}
}

@media (max-width: 480px) {
	.sub__wrap {
		padding-top: 20px;
		padding-bottom: 25px;
	}

	.sub__text { 
		margin-top: 0;
	}

	.sub__desc, .sub__subtitle {
		margin-left: 20px;
	}

	.sub__foto {
		display: block;
		max-width: 100%;
		height: auto;
	}		
}