.signup__info {
	padding: 30px 15px;
}

.signup__form {
	width: 360px;
	height: auto;
	font-family: 'Open Sans';
	margin: auto;
	font-weight: 700;
}

.form__title {
	display: flex;
	align-items: center;
	justify-content: center;	
	width: 100%;
	height: 53px;
	text-align: center;
	background-color: #f3f3f3;
	border-top-left-radius: 3px;
	border-top-right-radius: 3px;
	
}

.form__free {
	font-size: 20px;
	font-weight: bold;
	line-height: 1.2;		
	color: rgb(51, 51, 51);
	margin: 0;
}

.form__link {
	color: #ff8b38;
	text-transform: uppercase;
	margin: 0;
}

.form__text {
	margin: 0;
	font-size: 14px;		
	color: rgb(189, 189, 189);
	line-height: 1.2;
	padding-top: 28px;
  font-weight: 400;	
}

.signup__field {
	height: 255px;
	background-color: white;
	text-align: center;
	padding-left: 32px;
}

.signup__field p {
	padding-top: 16px;
	font-weight: 400;
	margin: 0;
}

.signup__field input {
	height: 54px;
	border: none;
	border-bottom: 1px #dedede solid;
	width: 90%;
	padding: 25px 10px 0px 20px;
}

input[type='submit'] {
	width: 100%;
	height: 60px;
	text-align: center;
	border-bottom-left-radius: 3px;
	border-bottom-right-radius: 3px;
	background-color: #ff8b38;
	font-size: 15px;
	color: #fff;
	border: none;
	padding: 15px;
}

@media (max-width: 480px) {
	.signup__form {
		width: auto;
	}
}