.footer {
	background-color: #000;
}

.footer__wrap {
	padding-top: 20px;
	padding-bottom: 10px;
}

.footer__list {
	list-style: none;
	padding: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0;
}

.footer__element {
	margin: 0 15px;

	&:first-child {
		margin-left: 0;
	}

	&:last-child {
		margin-right: 0;
	}
}


.footer__link {
  font-size: 24px;
  color: rgb(204, 204, 204);
	line-height: 1.2;
	transition-duration: 500ms;
	transition-property: color;

	&:hover {
		color: $primary;
	}
}

.footer__copy {
	font-size: 12px;
	color: rgba(255, 255, 255, 0.502);
	line-height: 1.2;
	text-align: center;
	margin-bottom: 0;
}

@media (max-width: 480px) {
	.footer__element {
		margin: 0 10px;
	
		&:first-child {
			margin-left: 0;
		}
	
		&:last-child {
			margin-right: 0;
		}
	}

	.footer__link {
		font-size: 20px;
	}
	
}
