.header {
	background-color: #000000;
	height: 70px;
}

.header__wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	padding-top: 20px;
	padding-bottom: 20px;
}

.header__logo {
	margin: 0;
}

.header__menu {
	padding: 0;
	margin: 0;
}

@media (max-width: 768px) {

	.header {
		height: 90px;
	}

	.header__wrap {
		flex-direction: column;
		justify-content: flex-start;
		padding-top: 25px;
		padding-bottom: 25px;
	}

	.header__logo {
    margin-left: 0;
		margin-bottom: 5px;
	}

}

@media (max-width: 480px) {
	.header {
		height: 75px;
	}

	.header__wrap {
		padding-top: 15px;
		padding-bottom: 15px;
	}

	.header__logo {
		margin-bottom: 3px;
	}
}



