.banner {
	background: url('../img/banner.jpg') center top no-repeat;
	background-size: cover;
}

.banner__wrap {
	display: flex;
	justify-content: space-between;
	padding-top: 110px;
	padding-bottom: 140px;
}

.banner__info {
	max-width: 530px;
	margin-right: auto;
}

.banner__logo {
	img {
		margin-bottom: 30px;
	}
	
}

.banner__title {
  font-size: 36px;
	font-family: "Raleway";
	font-weight: normal;
  color: rgb(255, 255, 255);
	line-height: 1.2;
	text-transform: uppercase;
	margin: 0;

	&::after {
		content: '';
		width: 65px;
		height: 2px;
		display: block;
		background-color: #fff;
		left: 0;
		top: 115px;
		margin: 40px 0;
	}
}

.banner__text {
  font-size: 16px;
  font-family: "Raleway";
  color: rgba(255, 255, 255, 0.8);
	line-height: 1.438;
	margin-bottom: 45px;
}

.banner__button {
	@include button() {
		display: block;
	};
}

@media (max-width: 768px) {
	.banner__wrap {
		flex-direction: column;
		justify-content: flex-start;
		padding-top: 80px;
		padding-bottom: 110px;
	}
}

@media (max-width: 480px) {
	.banner__title {
		font-size: 26px;
	}

	.banner__text {
		font-size: 14px;
	}

	.banner__button {
		margin: 0 auto;
	}

	.banner__wrap {
		padding-top: 55px;
		padding-bottom: 55px;
	}
}
  



