.picture {
	background-color: #f5f5f5;
}

.picture__wrap {
	display: flex;
	justify-content: space-between;
	padding-top: 90px;
	padding-bottom: 120px;	
}

.picture__title {
	font-size: 20px;
	color: rgb(33, 33, 33);
	font-weight: bold;
	line-height: 2.15;
	margin-bottom: 30px;

	&::after {
		content: '';
		width: 65px;
		height: 2px;
		background: $primary;
		display: block;
		margin-top: 20px;
	}
}

.picture__text {
	font-size: 14px;
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: left;
	margin-bottom: 30px;	
}

.picture__img {
	margin-left: 190px;
}

.picture__foto {
	box-shadow: 0 0 16px rgba(0, 0, 0, .5);
}

@media (max-width: 980px) {
	.picture__wrap {
		padding-top: 45px;
		padding-bottom: 60px;
		align-items: center;	
	}

	.picture__img {
		margin-left: 50px;
	}	
}

@media (max-width: 768px) {
	.picture__wrap {
		flex-direction: column;
		justify-content: flex-start;
	}
	.picture__img {
		margin: 0 auto;
	}
}

@media (max-width: 480px) {
	.picture__wrap {
		padding-top: 20px;
		padding-bottom: 25px;
	}

	.picture__title {
		margin-top: 0;
	}

	.picture__foto {
		display: block;
		max-width: 100%;
		height: auto;
	}		
}