.customer {
	background-color: #fff;	
}

.customer__wrap {
	padding-top: 90px;
	padding-bottom: 90px;
}

.customer__title {
	font-size: 36px;
	font-family: "Raleway";
	font-weight: normal;
	color: rgb(33, 33, 33);
	line-height: 1.194;
	text-align: center;
	text-transform: uppercase;

	&::after {
    background-color: $primary;
    width: 65px;
    height: 2px;
    display: block;
    content: '';
    margin: 25px auto;
  }
}

.customer__text {
	font-size: 14px;		
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: center;	
}

.customer__block {
	display: flex;
	justify-content: space-between;
	margin-top: 50px;
}

.customer__list	{
	flex-basis: 272px;
}

.customer__element {	
	font-size: 14px;
	font-style: italic;		
	color: rgb(134, 135, 139);
	line-height: 1.2;
	text-align: left;
	background: #f5f5f5;
	border-radius: 3px;
	padding: 25px 10px;
	margin: 0;
	position: relative;

	&::before {
		content: "";
		position: absolute;
    display: inline-block;
    width: 0;
		height: 0;
		bottom: -13px;
		left: 25px;
    border-style: solid;
    border-width: 15px 10px 0 10px;
    border-color: #f5f5f5 transparent transparent transparent;
	}
}

.customer__info {
	display: flex;
	flex-wrap: wrap;
	margin-top: 30px;
}

.customer__img {
	width: 70px;
	height: 70px;
}

.customer__catalog {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	margin-left: 15px;
}

.customer__name {
	font-size: 20px;
	color: rgb(51, 51, 51);
	font-weight: bold;
	line-height: 1.2;
	margin-top: 12px;
	margin-bottom: 5px;
}

.customer__subname {
	font-size: 14px;		
	color: rgb(134, 135, 139);
	line-height: 1.2;
	margin: 0;
}

@media (max-width: 980px) {
	.customer__wrap {
		padding-top: 45px;
		padding-bottom: 45px;
	}

	.customer__list	{
		flex-basis: 240px;
	}
}

@media (max-width: 768px) {
	.customer__block {
		flex-direction: column;
		justify-content: flex-start;
		margin-top: 40px;
	}

	.customer__info {
		justify-content: center;
	}

	.customer__wrap {
		padding-top: 15px;
		padding-bottom: 15px;
	}
}