.awecome {
	background-color: #fff;
}

.awesome__wrap {
	padding-top: 105px;
	padding-bottom: 105px;
}

.awesome__title {	
	font-size: 36px;
	font-family: "Raleway";
	font-weight: normal;
	color: rgb(33, 33, 33);
	line-height: 1.194;
	text-align: center;
	text-transform: uppercase;
	margin: 25px 0;
	
	&::after {
    background-color: $primary;
    width: 65px;
    height: 2px;
    display: block;
    content: '';
    margin: 25px auto;
  }
}

.awesome__text {	
	font-size: 14px;
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: center;		
}

.awesome__list {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.awesome__info {
	flex-basis: 360px;
	margin-top: 65px;
	text-align: center;
	padding: 0 15px;
}

.info__foto {
	display: flex;
	justify-content: center;
}

.info__foto i {
	width: 70px;
	height: 70px;
	font-size: 32px;
	border: 2px solid #ff8b38;
	border-radius: 50%;
	line-height: 1.2;
	color: #ff8b38;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}


.info__title {	
	font-size: 20px;	
	color: rgb(51, 51, 51);
	font-weight: bold;
	line-height: 1.2;
	text-align: center;
	margin-top: 30px;		
}

.info__text {	
	font-size: 14px;		
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: center;	
}

@media (max-width: 980px) {
	.awesome__wrap {
		padding-top: 50px;
		padding-bottom: 50px;
	}
}

@media (max-width: 768px) {
	.awesome__list {
		flex-direction: column;
		justify-content: flex-start;
	}

	.awesome__info {
		flex-basis: auto;
	}
}

@media (max-width: 480px) {
	.awesome__wrap {
		padding-top: 25px;
		padding-bottom: 25px;
	}

	.awesome__info {
		margin-top: 30px;
	}
}