.styl {
	background: url('../img/styl.jpg') center top no-repeat;
	background-size: cover;
}

.styl__wrap {
	padding-top: 70px;
	padding-bottom: 60px;
	text-align: center;
}

.styl__title {	
	font-size: 36px;
	font-family: "Raleway";
	font-weight: 400;
	color: rgb(255, 255, 255);
	line-height: 1.2;
	text-transform: uppercase;

	&::after {
    background-color: #fff;
    width: 65px;
    height: 2px;
    display: block;
    content: '';
    margin: 25px auto;
  }
}

.styl__text {
	font-size: 16px;
	font-family: "Raleway";
	color: rgba(255, 255, 255, 0.8);
	line-height: 1.2;	
}

.styl__button {
	@include button() {
		display: inline-block;
		margin-top: 15px;
	};
}

@media (max-width: 480px) {
	.styl__wrap {
		padding-top: 15px;
		padding-bottom: 25px;
	}
}