.menu {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
}

.menu__element {
	margin-right: 50px;
	&:last-child {
		margin-right: 0;
	}
}

.menu__link {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.69);
	line-height: 1.2;
	display: inline-block;
	text-decoration: none;
	transition-duration: 500ms;
	transition-property: color;

	&:hover {
		color: $primary;
	}
	
}

@media (max-width: 768px) {
	.menu__element {
		&:last-child {
			margin-right: 0;
		}
	}
}

@media (max-width: 480px) {
	.menu__element {
		margin-right: 15px;
		margin-bottom: 10px;
	}

	.menu__link {
		font-size: 13px;
	}
}
