.price {
	background-color: #f5f5f5;
}

.price__wrap {
	padding-top: 100px;
	padding-bottom: 95px;
}

.price__title {
	font-size: 36px;
	font-family: "Raleway";
	font-weight: normal;
	color: rgb(33, 33, 33);
	line-height: 1.194;
	text-align: center;	
	text-transform: uppercase;

	&::after {
    background-color: $primary;
    width: 65px;
    height: 2px;
    display: block;
    content: '';
    margin: 25px auto;
  }
}

.price__text {
	font-size: 14px;
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: center;
}

.price__block {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	margin-top: 80px;
}

.price__gray {
	background-color: #333;
}

.price__info {
	width: 345px;
	height: auto;
}

.price__option {
	font-size: 20px;
	color: rgb(255, 255, 255);
	font-weight: bold;
	line-height: 1.2;
	text-align: center;
	margin: 0;
	padding: 18px 15px;
	border-bottom: 1px solid #fff;
}

.price__tariff {
	text-align: center;
}

.price__numbers {
	margin: 0;
	padding: 0 15px;
}

.price__middle {
	font-size: 35px;
	font-family: "Raleway";
	color: rgb(255, 255, 255);
	font-weight: bold;
	line-height: 1;	
}

.price__large {
	font-size: 95px;
	font-family: "Raleway";
	color: rgb(255, 255, 255);
	font-weight: bold;
	line-height: 1;	
}

.price__description {	
	font-size: 14px;
	font-style: italic;
	color: rgba(255, 255, 255, 0.502);
	line-height: 1.714;
	text-align: center;
	margin: 0;
	padding: 15px;
	border-bottom: 5px solid $primary;
}

.price__orang {	
	font-size: 14px;
	color: rgb(255, 255, 255);
	font-weight: bold;
	line-height: 1.714;
	text-transform: uppercase;
	background: $primary;
	margin: 0;
	padding: 3px 15px 7px 15px;
}

.price__list {
	background: #fff;
	margin: 0;
	padding: 0;
	list-style: none;
	text-align: center;
}

.price__element {	
	font-size: 14px;
	color: rgb(134, 135, 139);
	text-transform: uppercase;
	line-height: 1.714;
	padding: 10px;
	border-bottom: 1px solid #f5f5f5;	
}

@media (max-width: 1200px) {
	.price__info {
		width: 300px;
	}
}

@media (max-width: 980px) {
	.price__wrap {
		padding-top: 50px;
		padding-bottom: 50px;
	}

	.price__info {
		width: 240px;
	}
}

@media (max-width: 768px) {
	.price__block {
		flex-direction: column;
		justify-content: flex-start;
		margin-top: 40px;
	}

	.price__info {
		width: 100%;
		margin-top: 20px;
	}
}

@media (max-width: 480px) {
	.price__wrap {
		padding-top: 15px;
		padding-bottom: 25px;
	}

	.price__block {
		margin-top: 0;
	}
}






	

	
