.contact {
	background-color: #fff;
}

.contact__wrap {
	padding-top: 100px;
	padding-bottom: 80px;
}

.contact__title {
	font-size: 36px;
	font-family: "Raleway";
	font-weight: normal;
	color: rgb(33, 33, 33);
	line-height: 1.194;
	text-align: center;
	text-transform: uppercase;
	
	&::after {
    background-color: $primary;
    width: 65px;
    height: 2px;
    display: block;
    content: '';
    margin: 25px auto;
  }
}

.contact__text {	
	font-size: 14px;	
	color: rgb(134, 135, 139);
	line-height: 1.429;
	text-align: center;	
}

.contact__list {
  list-style: none;
  padding: 0;
	display: flex;
	justify-content: space-between;
	margin-top: 75px;
}

.contact__left {
  flex-basis: 530px;
  margin: 10px 0;
}

.contact__right {
	flex-basis: 530px;
  margin: 10px 0;
}

.form {
  label {
    @include visually-hidden;
  }
}

.form__input {
	margin-bottom: 20px;
}

input, textarea {
	width: 100%;
	display: block;
	border: none;
	border-bottom: 1px solid #d6d6d6;
	box-shadow: none;
	resize: none;
	font-size: 14px;
	color: rgb(189, 189, 189);
	line-height: 1.2;
	padding: 30px 10px 5px 15px;
	box-sizing: border-box;

	&::placeholder {
		color: rgb(189, 189, 189);
		opacity: 1;
	}
}

textarea {
	min-height: 195px;
}

.contact__button {
	@include button() {
		width: 145px;
		height: 45px;
		background-color: #ff9b51;
		border: 2px solid #ff9b51;
		border-radius: 4px;
		margin: 0 auto;
	};
}

@media (max-width: 1100px) {

  .contact__list {
    flex-direction: column;
  }

  .contact__left {
		flex-basis: 100%;
    margin-right: 0;
    margin-bottom: 35px;
	}

	.contact__right {
		width: 100%;
		flex-basis: auto;
	}
}

@media (max-width: 980px) {
	.contact__wrap {
		padding-top: 50px;
		padding-bottom: 40px;
	}
}

@media (max-width: 480px) {
	.contact__wrap {
		padding-top: 15px;
		padding-bottom: 30px;
	}

	.contact__list {
		margin-top: 30px;
		margin-bottom: 0;
	}

	.contact__title {
		margin-top: 15px;
	}

	.contact__left {
		margin-bottom: 0;
	}

	.contact__right {
		margin-top: 0;
	}
}









